import React, { memo } from 'react';
import './notFound.scss';

const NotFound = () => {
  const back = () => {
    history.back()
  }
  return (
    <div className='not-found-section'>
      <div className='guide-ment'>
        <h1>   .</h1>
        <br /><br />
        <h2> : '{location.pathname}'</h2>
        <br /><br />
        <h3>.. 404  ?<br />    ..<br /> ?</h3>
        <br /><br />
        <button onClick={back}>go back</button>
      </div>
    </div>
  )
}

export default memo(NotFound);